.App {
  font-family: sans-serif;
  text-align: center;
  display: flex;
  flex-direction: row;
  background-color: ;
  padding: 40px;
}

.user-container {
  width: 40%;
  border-width: 2px;
  border-style: solid;
  margin-right: 30px;
  border-color: rgb(74, 85, 33);
  align-self: left;
}

.bio-container {
  width: 60%;
}

.bio-image {
  height: 100px;
  width: 100px;
  border-radius: 50px;
  margin-top: 20px;
  margin-right: 30px;
  border-style: solid;
  border-color: rgb(74, 85, 33);
  font-size: 11px;
  text-align: center;
}

.name-container {
  display: flex;
  flex-direction: row;
}

.phone-name {
  width: 30%;
  border-width: 2px;
  border-style: solid;
  border-color: rgb(74, 85, 33);
  padding-top: 15px;
  margin-top: 15px;
  background-color: none;
}

.name-colour {
  color: brown;
  font-size: 15px;
  font-weight: 600;
}
.number {
  color: yellowgreen;
  font-size: 14px;
  font-weight: 600;
}

.bio-data {
  color: rgb(121, 73, 2);
  font-size: 15px;
  font-weight: 500;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-align: left;
  padding: 10px;
  border-width: 2px;
  border-style: solid;
  border-color: rgb(74, 85, 33);
  background-color: none;
}
